header {
  height: 100vh;
  padding-top: 2rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100vh;
  position: relative;
}

.cta {
  margin-top: 1rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

.header__socials a:hover {
  font-size: 1.5rem;
  font-weight: bolder;
}
.me {
  background: var(--color-bg);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  /* left: 50%; */
  /* transform: translateX(-50%); */
  margin-top: 2rem;
  /* border-radius: 12rem 12rem 0rem 0rem; */
  border-radius: 2rem 2rem 2rem 2rem;
  overflow: hidden;
  /* padding: 0.5rem 1.5rem 1.5rem 1.5rem; */
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}
.scroll__down:hover {
  font-weight: bolder;
  font-size: 1rem;
}
/*================= sm ==========*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}
/* 

/*================= sm ==========*/

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    margin: 0 auto;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}
