.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15rem;
}




.about__card{
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
}


.about__icon{
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
};

.about__card h5 {
  font-size: 0.95rem;
}
.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
};

.about__content p{
  margin: 2rem 0 6rem;
  color: var(--color-light);
}

